<template>
  <ol :class="{'mobile':mobile}">
    <li
      v-for="item in content" 
      :key="item.name"
    >
      <a
        :href="`#${item.name.split(' ').join('')}`"
      >
        {{item.name}}
      </a>
      <ul v-if="item.subTitles">
        <li v-for="subs in item.subTitles" :key="subs">
          <a
            :href="`#${subs.split(' ').join('')}`"
          >
            {{ subs }}
          </a>
        </li>
      </ul>
    </li>
  </ol>
</template>

<script>

export default {
  name: 'asideList',
  props: {
    content: {
      type: Array,
      default: () => ([])
    },
    mobile: {
      type: Boolean,
      default: false
    }
  }
}
</script>

<style lang="scss" scoped>
  @import '@/assets/styles/utils/vars.scss';
  
  ol {
    color: $mainFontColor;
    margin: 0 2rem;
    counter-reset: my-awesome-counter;

    &.mobile {
      color: #fff;
      font-size: 14px;
      a {
        color: #fff;
        font-size: 14px;
      }
    }
  }
  ol > li {
    counter-increment: my-awesome-counter;
    display: inline;
  }
  ol > li::before {
    content: counter(my-awesome-counter) ". ";
  }
  ul > li {
    list-style: square inside;
  }
  a {
    color: $mainFontColor;
    cursor: pointer;
  }
</style>
